import React, { Component } from 'react';
import { userService } from '../services/userService.js';
import { bitcoinService } from '../services/bitcoinService';
import { Redirect } from 'react-router-dom/cjs/react-router-dom.min';
import { MoveList } from '../cmps/MoveList';

export class HomePage extends Component {
    state = {
        user: null,
        btc: null,
    };
    async componentDidMount() {
        const user = userService.getUser();
        this.setState({ user });
        if (!user) this.props.history.push('/signup');
        else {
            const btc = await bitcoinService.getRate(user.coins);
            this.setState({ user, btc });
        }
    }
    onLogout = () => {
        userService.logout();
        const user = null;
        this.setState({ user }, () => {
            this.props.history.push('/signup');
        });
    };
    get lastUserMoves() {
        const moves = [...this.state.user.moves];
        const lastmoves = moves.slice(0, 3);
        console.log('lastmoves', lastmoves);
        return lastmoves;
    }
    render() {
        if (!this.state.user) return <div></div>;
        return (
            <div className="home-page">
                <section>
                    <div>
                        <h2>Hello, {this.state.user.name}</h2>
                        <button className="btn" onClick={this.onLogout}>
                            Logout
                        </button>
                    </div>

                    <img
                        src={`https://robohash.org/${this.state.user.name}?set=set5`}
                        alt=""
                    />
                    <h2>USD : {this.state.user.coins}$</h2>
                    <h2>btc : {this.state.btc}</h2>
                    {this.state.user.moves.length ? (
                        <MoveList
                            title={'Your last 3 transactions'}
                            moveList={this.lastUserMoves}
                        />
                    ) : (
                        <h3>no transactions yet</h3>
                    )}
                </section>
            </div>
        );
    }
}
