import './assets/style/style.scss'
import { AppHeader } from './cmps/AppHeader'
import { ContactPage } from './pages/ContactPage'
import { ContactEdit } from './pages/ContactEditPage'
import { ContactDetails } from './pages/ContactDetailsPage'
import { StatisticsPage } from './pages/StatisticsPage'
import { HomePage } from './pages/HomePage'
import { HashRouter as Router, Route, Switch } from 'react-router-dom';
import React from 'react'
import { Signup } from './pages/SignupPage'

export function App() {
  return (
    <Router>
      <div>
        <AppHeader />
        <main className="container">
          <Switch>
            <Route component={ContactEdit} path='/Contact/edit/:id?' />
            <Route component={ContactDetails} path='/Contact/:id' />
            <Route component={ContactPage} path='/Contact' />
            <Route component={StatisticsPage} path='/statistics' />
            <Route component={Signup} path='/Signup' />
            <Route component={HomePage} path='/' />
          </Switch>
        </main>
      </div>
    </Router>

  )
}
