import React, { Component } from 'react'
import { connect } from 'react-redux'
import { ContactList } from './../cmps/ContactList'
import { ContactFilter } from './../cmps/ContactFilter'
import contactService from '../services/contactService';
import { ContactDetails } from './ContactDetailsPage'
import { Link } from 'react-router-dom'
import { loadContacts, removeContact,setFilterBy } from '../store/actions/contactActions'
import {userService} from '../services/userService'

class _ContactPage extends Component {
    
     componentDidMount() {
        this.props.loadContacts();
        const user = userService.getUser()
        if (!user) this.props.history.push('/signup')

    }
    removeContact = async (contactId) => {
        this.props.removeContact(contactId)
    }
    // async loadContacts() {
    //     const { filterBy } = this.state
    //     console.log(filterBy);
    //     const contacts = await contactService.getContacts(filterBy)
    //     this.setState({ contacts })
    // }
    onChangeFilter = (filterBy) => {
        this.props.setFilterBy(filterBy)
        console.log(filterBy);
        this.props.loadContacts()

    }
    render() {
        const { contacts } = this.props
        if (!contacts) return <div>loading..</div>
        return (
            <div className="contact-page">
                <div className="filter-add-container"><ContactFilter onChangeFilter={this.onChangeFilter} />

                <Link to='/contact/edit'></Link></div>
                
                <ContactList contacts={contacts} removeContact={this.removeContact} />
            </div>
        )
    }
}

const mapStateToProps = state => {

    return {
        contacts: state.contactModule.contacts,
    }
}

const mapDispatchToProps = {
    loadContacts,
    removeContact,
    setFilterBy
}

export const ContactPage = connect(mapStateToProps, mapDispatchToProps)(_ContactPage)