import axios from 'axios'
export const bitcoinService = {
    getRate,
    getMarketPrice,
    getConfirmedTransactions,
    getTradeVolume
}

async function getRate(coins) {
    try {
        const res = await axios.get(`https://blockchain.info/tobtc?currency=USD&value=${coins}`);
        return res.data;
    } catch (err) {
        console.log('error in bitcoin service', err);
        throw err;
    }
}
async function getMarketPrice() {
    const { data } = await axios.get(`https://api.blockchain.info/charts/market-price`, {
        params: {
            timespan: '5months',
            format: 'json',
            cors: true,
        },
    })
    return data;
}
async function getTradeVolume() {
    const { data } = await axios.get(`https://api.blockchain.info/charts/trade-volume`, {
        params: {
            timespan: '5months',
            format: 'json',
            cors: true,
        },
    })
    console.log('', data);
    return data;
}

