import { NavLink, withRouter } from "react-router-dom";

import React from 'react'

function _AppHeader({ onChangeRoute }) {
    return (
        <header className='app-header '>
            <NavLink exact to='/'><div className="logo"><h1>Mister</h1><h1>BitC<span></span>in</h1></div></NavLink>
            <nav className="links">
                <NavLink to='/contact'>Contacts</NavLink> 
                <NavLink exact to='/statistics'>Statistics
                </NavLink>
            </nav>
        </header>
    )
}
export const AppHeader = withRouter(_AppHeader)