import React, { Component } from 'react'
import contactService from '../services/contactService'
import { connect } from 'react-redux'
import { saveContact,getContactById } from '../store/actions/contactActions'

class _ContactEdit extends Component {
    state = {
        contact: null
    }
    async componentDidMount() {
        const contactId = this.props.match.params.id
        const contact = contactId ? await this.props.getContactById(contactId) : contactService.getEmptyContact()
        this.setState({ contact })
    }
    handleChange = ({ target }) => {
        const field = target.name
        const value = target.type === 'number' ? +target.value : target.value
        this.setState(prevState => ({ contact: { ...prevState.contact, [field]: value } }))
    }
    onSaveContact = async (ev) => {
        ev.preventDefault()
        await this.props.saveContact({ ...this.state.contact })
        this.props.history.push('/')
    }
    render() {
        const { contact } = this.state
        if (!contact) return <div>Loading...</div>
        return (
            <div className='contact-edit'>
                <section>
                <h1>{contact._id ? 'Edit' : 'Add'} Contact</h1>
                <form onSubmit={this.onSaveContact} >
                    <label htmlFor="name">name</label>
                    <input onChange={this.handleChange} value={contact.name} type="text" name="name" id="name" />
                    <label htmlFor="type">email</label>
                    {/* <select onChange={this.handleChange} value={contact.type} name="type" id="type">
                        <option value="" disabled>Select A Type</option>
                        <option value="Cooking" >Cooking</option>
                        <option value="Cleaning" >Cleaning</option>
                        <option value="Pleasure" >Pleasure</option>
                        <option value="Office" >Office</option>
                    </select> */}
                    <input onChange={this.handleChange} value={contact.email} type="text" name="email" id="email" />
                    <label htmlFor="phone">phone</label>
                    <input onChange={this.handleChange} value={contact.phone} type="text" name="phone" id="phone" />

                    <button className="btn">Save</button>
                </form></section>
            </div>
        )
    }
}
const mapStateToProps = state => {

    return {
        contacts: state.contactModule.contacts,
    }
}

const mapDispatchToProps = {
    saveContact,
    getContactById
}

export const ContactEdit = connect(undefined, mapDispatchToProps)(_ContactEdit)