import React, { Component } from 'react'

export class TransferFund extends Component {
    state={
        amount:0
    }
    handleChange = ({ target }) => {
        const field = target.name
        const value = target.type === 'number' ? +target.value : target.value
        this.setState({ [field]: value })
    }
    onTransfer=(ev)=>{
        ev.preventDefault()
        this.props.onTransferCoins(this.state.amount);
    }
    render() {
        const {amount} = this.state;
        return (
            <div className="transfer-fund">
                <form onSubmit={this.onTransfer}>
                    <label htmlFor="amount">Enter amount to transfer: </label>
                    <input type="number" name="amount" id="amount" onChange={this.handleChange} value={amount} max={this.props.maxCoins}/>
                            <small>no more then {this.props.maxCoins}</small>

                    <button><span></span> Transfer</button><br />
                </form>
            </div>
        )
    }
}
