import React, { Component } from 'react';
import {
    LineChart,
    Line,
    XAxis,
    YAxis,
    CartesianGrid,
    Tooltip,
    Legend,
    ResponsiveContainer,
} from 'recharts';
import { bitcoinService } from '../services/bitcoinService';

export function Chart({ data }) {
    console.log(data);
    return (
        <div>
            <h2>{data.name}</h2>
            <h3>{data.desc}</h3>
            <div
                className="chart"
                style={{
                    backgroundColor: '#272727',
                }}
            >
                <ResponsiveContainer width="100%" height="100%">
                    <LineChart
                        width={500}
                        height={300}
                        data={data.values}
                        margin={{ top: 5, right: 20, bottom: 5, left: 0 }}
                    >
                        <Line
                            type="monotone"
                            dataKey="y"
                            stroke="rgb(104 141 184)"
                            dot={false}
                        />
                        <XAxis dataKey="x" />
                        <YAxis dataKey="y" />
                    </LineChart>
                </ResponsiveContainer>
            </div>
        </div>
    );
}
