import React from 'react'
import { Link } from "react-router-dom"

export function ContactPreview({ contact, removeContact }) {
    function onRemoveContact(ev) {
        ev.stopPropagation();
        ev.preventDefault();
        removeContact(contact._id)
    }
    return (
        <article className="contact-preview">  
            <Link  to={`/contact/${contact._id}`}>
                <section className="contact">
                    <img src={`https://robohash.org/${contact._id}?set=set5`} alt="" />
                    <h3>{contact.name}</h3></section>
                <section className="actions"> 
                     <Link to={`/contact/edit/${contact._id}`}></Link>
                     <button onClick={onRemoveContact}></button>
                </section> 
            </Link>
        </article>
    )
}
