import { storageService } from "./storage.service.js"

const dbKey = 'user';

export const userService = {
    getUser, signup, addMove,logout
}

function signup(name) {
    const user = { name, coins: 100, moves: [] }
    storageService.store(dbKey, JSON.parse(JSON.stringify(user)))
}
function logout(){
    storageService.remove(dbKey);
}

function addMove(contact, amount) {
    console.log(contact);
    const user = storageService.load(dbKey)
    const move = { to: contact.name, amount, createdAt: Date.now() }
    user.coins = user.coins-amount;
    user.moves.unshift(move)
    storageService.store(dbKey, user)
    return user;
}

function getUser() {
    var user = storageService.load(dbKey)
    if (user) return user
    else return null;
    // else {
    //     user = {
    //         name: "Ochoa Hyde", coins: 100, moves: []
    //     }
    //     storageService.store(dbKey, user)
    //     return storageService.load(dbKey)
    // }
}