import React, { Component } from 'react';
import { Chart } from '../cmps/Chart';
import { userService } from '../services/userService';
import { bitcoinService } from '../services/bitcoinService';

export class StatisticsPage extends Component {
    state = {
        marketPrice: null,
        tradeVolume: null,
    };
    async componentDidMount() {
        const user = userService.getUser();
        if (!user) this.props.history.push('/signup');
        else {
            const dataMarketPrice = await bitcoinService.getMarketPrice();
            const dataTradeVolume = await bitcoinService.getTradeVolume();

            this.setState({
                marketPrice: {
                    name: dataMarketPrice.name,
                    desc: dataMarketPrice.description,
                    values: this.getDate(dataMarketPrice.values),
                },
                tradeVolume: {
                    name: dataTradeVolume.name,
                    desc: dataTradeVolume.description,
                    values: this.getDate(dataTradeVolume.values),
                },
            });
        }
    }
    getDate = (values) => {
        return values.map((item) => {
            const obj = Object.assign({}, item);
            obj.x = new Date(obj.x * 1000).toLocaleDateString();
            return obj;
        });
    };

    render() {
        const { marketPrice, tradeVolume } = this.state;
        if (!marketPrice || !tradeVolume) return <div>loading..</div>;
        return (
            <div>
                <Chart data={marketPrice} />
                <Chart data={tradeVolume} />
            </div>
        );
    }
}
