import { Component } from 'react'
import contactService from '../services/contactService'
import { userService } from '../services/userService'
import { TransferFund } from '../cmps/TransferFund'
import {MoveList} from '../cmps/MoveList'
import { getContactById } from '../store/actions/contactActions'
import { connect } from 'react-redux'

class _ContactDetails extends Component {
    state = {
        contact: null,
        user: null,
    }

    componentDidMount() {
        this.loadContact()
        const user = userService.getUser()
        if (!user) this.props.history.push('/signup')
        else this.setState({ user })
    }
    componentDidUpdate(prevProps, prevState) {

        if (prevProps.match.params.id !== this.props.match.params.id) {
            this.loadContact()
        }

    }
    async loadContact() {

        const contact = await this.props.getContactById(this.props.match.params.id)
        this.setState({ contact })
    }
    onTransferCoins=(amount)=> {
        const user= userService.addMove(this.state.contact,amount)
        this.setState({user})
    }
    get moveList(){
        const moves = [...this.state.user.moves];
        return moves.filter(move=>move.to===this.state.contact.name)
    }

    render() {
        const { contact, user } = this.state
        if (!contact) return <div>Loading..</div>
        return (
            <div className='contact-details'>
                <section>
                <h1>{contact.name}</h1>
                <h3><span className="phone-icon"></span> {contact.phone}</h3>
                <h3><span className="email-icon"></span> {contact.email}</h3>
                <img src={`https://robohash.org/${contact._id}?set=set5`} alt="" />
                {/* <button onClick={this.props.goBack} >Back</button> */}
                <TransferFund contact={contact} maxCoins={this.state.user.coins} onTransferCoins={this.onTransferCoins} />
               {this.moveList.length? <MoveList title={'Your transactions to this contact'} moveList={this.moveList}/>:<h3>no transactions were made to this contact</h3>}
</section>
            </div>
        )
    }
}


const mapDispatchToProps = {
    getContactById
}

export const ContactDetails = connect(undefined, mapDispatchToProps)(_ContactDetails)